<template>
<div>
  <Form ref="ApplyCard" :model="applyCard" :rules="applyCardRule" label-position="top" style="max-width: 300px">
    <FormItem>
      <h4></h4>
    </FormItem>
    <FormItem label="" prop="name">
      <Input v-model="applyCard.name" placeholder="" style="width: 300px" clearable></Input>
    </FormItem>
    <FormItem label="" prop="address">
      <Input v-model="applyCard.address" placeholder="" style="width: 300px" clearable></Input>
    </FormItem>
    <FormItem label="" prop="phone">
      <Input type="text" v-model="applyCard.phone" placeholder=""></Input>
    </FormItem>
    <FormItem>
      <Button type="error" @click="handleSubmit('ApplyCard')"></Button>
    </FormItem>
  </Form>

  <h4></h4>
  <Page :current="paging.curr_page" :total="paging.total" simple @on-change="changePage($event)"></Page>
  <Table stripe :columns="applyListCol" :data="applyList"></Table>
</div>
</template>

<script>
export default {

  name: 'ApplyCard',

  data () {
    return {
      statusLookUp: {
        '0': '',
      },
      applyCard: {
        name: '',
        address: '',
        phone: '',
      },
      applyCardRule: {
        name: [
          { required: true, message: '', trigger: 'blur' },
        ],
        address: [
          { required: true, message: '', trigger: 'blur' },
        ],
        phone: [
          { required: true, message: '', trigger: 'blur' },
        ],
      },
      applyListCol: [
        {
          title: '',
          key: 'id',
          width: 120,
        },
        {
          title: '',
          key: 'nickname',
          width: 120,
        },
        {
          title: '',
          key: 'address',
          width: 240,
        },
        {
          title: '',
          key: 'phone',
          width: 160,
        },
        {
          title: '',
          key: 'status',
          width: 100,
        },
        {
          title: '',
          key: 'updated_at',
          width: 120,
        },
      ],
    }
  },
  computed: {
    applyList () {
      let list = this.$store.getters.applyList
      return list.map(item => {
        item.status = this.statusLookUp[item.status]
        return item
      })
    },
    paging () {
      return this.$store.getters.paging('wallet', 'applyList')
    },
  },
  methods: {
    applyUserCard () {
      const data = {
        address: this.applyCard.address,
        nickname: this.applyCard.name,
        phone: this.applyCard.phone,
      }
      try {
        this.$store.dispatch('ApplyCard', data)
      } catch (e) {
        console.log(e)
        return 'fail'
      }
      return 'success'
    },
    handleSubmit (name) {
      this.$refs[name].validate(async (valid) => {
        if (valid) {
          let response = await this.applyUserCard()
          if (response !== 'success') {
            this.$Message.error('')
            return
          }
          this.$Message.success('!')
          this.handleReset('ApplyCard')
          this.$store.dispatch('GetCardApplyList')
        } else {
          this.$Message.error('!')
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
  },
}
</script>

<style lang="css">
</style>



